body {
  min-width: 320px;
  font-family: "Bloggersans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #444444;
  background-color: #edeff6;
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
}
@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}
.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
.page-header {
  padding-top: 73px;
  background-color: #161c34;
}
.page-header--inner-page {
  padding-top: 57px;
}
.page-header .contacts-block {
  display: none;
}
.page-header .user-list__login {
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
}
@media (min-width: 768px) {
  .page-header {
    padding-top: 132px;
  }
  .page-header--inner-page {
    background-color: #161c35;
  }
  .page-header .user-block {
    display: block;
  }
}
@media (max-width: 1439px) {
  .page-header--no-js {
    padding-top: 0;
  }
  .page-header--no-js .main-nav {
    position: static;
  }
  .page-header--no-js .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
  .page-header {
    padding-top: 99px;
  }
  .page-header .contacts-block {
    display: flex;
  }
  .page-header .user-block {
    display: flex;
  }
}
.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-list__item {
  font-size: 30px;
  line-height: 36px;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
}
.site-list__link {
  color: #1d2e5b;
}
@media (min-width: 768px) {
  .site-list__item {
    font-size: 60px;
    line-height: 71px;
    text-align: left;
  }
  .site-list__link {
    letter-spacing: -0.012em;
  }
}
@media (min-width: 1440px) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
    max-width: 700px;
  }
  .site-list__item {
    margin: 0 18px;
    line-height: 0;
  }
  .site-list__link {
    display: inline-block;
    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
    overflow: hidden;
  }
  .site-list__link:active {
    opacity: 0.3;
  }
  .site-list__link span {
    position: relative;
    display: inline-block;
    transition: transform 0.3s;
  }
  .site-list__link span::before {
    content: attr(data-hover);
    position: absolute;
    top: 100%;
    color: #ffd74b;
    transform: translate3d(0, 0, 0);
  }
  .site-list__link span:hover {
    transform: translateY(-100%);
  }
}
.user-list {
  margin: 0;
  padding: 0;
  list-style: none;
  flex-grow: 1;
}
.user-list__login {
  display: inline-block;
  width: 189px;
  padding: 5px 30px;
  box-sizing: border-box;
  font-size: 17px;
  line-height: 30px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  color: #161c35;
  background-color: #ffd74b;
  border-radius: 25px;
}
.user-list__login:hover {
  background-color: #ffbd30;
}
.user-list__login:active span {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .user-list__login {
    min-width: 189px;
    font-size: 20px;
  }
}
.contacts {
  margin: 0;
  padding: 0;
  list-style: none;
}
.contacts__item {
  position: relative;
  display: flex;
  align-items: center;
  height: 45px;
  font-weight: 700;
  text-transform: uppercase;
}
.contacts__link {
  display: block;
  padding-left: 61px;
  color: #192144;
  fill: #ffd74b;
}
.contacts__link:hover .contacts__link-img svg {
  fill: #ffbd30;
}
.contacts__link:active .contacts__link-img svg {
  fill: rgba(255, 189, 48, 0.3);
}
.contacts__link-img {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background-color: #121936;
}
.contacts--yellow .contacts__link {
  color: #ffd74b;
}
.contacts--yellow .contacts__link:hover {
  color: #ffbd30;
}
.contacts--yellow .contacts__link:active {
  color: rgba(255, 189, 48, 0.3);
}
@media (min-width: 768px) {
  .contacts__item {
    height: 68px;
    font-size: 28px;
    line-height: 28px;
  }
  .contacts__link {
    padding-left: 91px;
  }
  .contacts__link-img {
    width: 68px;
    height: 68px;
  }
  .contacts__link-img svg {
    width: 27px;
    height: 27px;
  }
}
@media (min-width: 1440px) {
  .contacts__link {
    padding-left: 122px;
  }
  .contacts__link--tel {
    margin-right: 110px;
  }
  .contacts__item {
    height: 90px;
  }
}
.socials {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.socials__item {
  font-size: 0;
  line-height: 0;
}
.socials__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  border-radius: 50%;
}
.socials__link:hover .socials__img {
  opacity: 0.6;
}
.socials__link:active .socials__img {
  opacity: 0.3;
}
.socials__img {
  fill: #192144;
}
@media (min-width: 768px) {
  .socials__link {
    width: 60px;
    height: 60px;
  }
  .socials__img--fb {
    width: 14px;
    height: 28px;
  }
  .socials__img--vk {
    width: 31px;
    height: 16px;
  }
  .socials__img--instagram {
    width: 24px;
    height: 24px;
  }
}
