.steps {
  margin: 0;
  padding: 0;
  list-style: none;
}
.steps__item {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding: 20px 22px 35px 22px;
  margin-top: -10px;
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
  color: #161c35;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-sizing: border-box;
}
.steps__item:first-child {
  margin-top: 0;
}
.steps__item:last-child {
  padding-bottom: 25px;
  border-radius: 10px;
}
.steps__item--one {
  background-color: #ff8d30;
}
.steps__item--two {
  background-color: #ffa530;
}
.steps__item--three {
  background-color: #ffbd30;
}
.steps__item--four {
  background-color: #ffd74b;
}
.steps__text {
  width: 180px;
  margin: 0;
}
.steps__number {
  margin: 0;
  color: #ffffff;
}
@media (min-width: 768px) {
  .steps {
    display: flex;
    flex-wrap: wrap;
    border-radius: 20px;
    overflow: hidden;
  }
  .steps__item {
    flex-direction: column;
    width: 50%;
    padding: 27px 33px;
    margin-top: 0;
    font-size: 24px;
    line-height: 24px;
    border-radius: 0;
  }
  .steps__item:nth-child(odd) {
    width: calc(50% + 20px);
    margin-right: -20px;
  }
  .steps__item:nth-child(2) {
    border-top-left-radius: 20px;
  }
  .steps__item:last-child {
    border-radius: 0;
    border-bottom-left-radius: 20px;
  }
  .steps__text {
    width: 230px;
    margin-bottom: 9px;
  }
  .steps__number {
    font-size: 30px;
    line-height: 30px;
  }
}
@media (min-width: 1440px) {
  .steps {
    flex-wrap: nowrap;
  }
  .steps__item {
    flex-direction: row;
    padding-top: 24px;
    padding-bottom: 32px;
    padding-left: 31px;
    border-top-left-radius: 20px;
